body {
  position: relative;
  background: #01343f;
  z-index: 3;
  font-family: "Franklin Gothic Medium";
  font-weight: 400;
  color: #f1cb90;
}
body::before {
  content: "";
  position: absolute;
  z-index: -2;
  top: 0;
  left: 0;
  width: 100vw;
  display: block;
  height: 1000px;
  max-height: 100%;
  background: url("../img/main_bg.png") top no-repeat;
  background-size: cover;
}

* {
  font-family: "Franklin Gothic Medium";
  font-weight: 400;
  color: #f1cb90;
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1700px;
}

.nav {
  padding: 70px 0 0;
  position: relative;
  overflow: hidden;
}
.nav__logo {
  max-width: 442px;
  margin: 0 -25px 0 auto;
}
.nav__logo img {
  width: 100%;
}
.nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.catalog {
  padding: 9px 0 0;
}
.catalog__info {
  width: max-content;
  margin: 0 0 0 auto;
  position: relative;
}
.catalog__info img {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  object-fit: contain;
  z-index: 2;
  height: 100%;
  object-position: right;
  pointer-events: none;
}
.catalog h1 {
  font-size: 80px;
  color: #f1cb90;
  text-align: right;
  line-height: 93%;
  letter-spacing: 0.35rem;
  margin: 29px 0 0;
  opacity: 0;
}
.catalog__subtitle {
  color: #f1cb90;
  text-align: right;
  line-height: 93%;
  font-size: 58px;
  letter-spacing: 0.06em;
  opacity: 0;
}
.catalog__title {
  font-size: 36px;
  color: #f1cb90;
  text-align: right;
  letter-spacing: 0.17rem;
  opacity: 0;
}
.catalog__wrapper {
  margin: 107px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 50px;
}
.catalog__item {
  padding: 20px;
  background: #aaa9a0;
  display: flex;
  flex-direction: column;
}
.catalog__item img {
  width: 100%;
  object-fit: cover;
  display: block;
  transition: all 0.3s;
}
.catalog__item p {
  color: #285066;
  font-size: 24px;
  text-align: center;
  padding: 0 0 20px;
}
.catalog__item span {
  margin: auto 0 0;
  display: block;
  position: relative;
  overflow: hidden;
}
.catalog__item:hover img {
  transform: scale(1.15);
}

.footer {
  padding: 200px 0 50px;
}
.footer__list {
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 50px;
}
.footer__list a {
  font-size: 26px;
  color: #f1cb90;
  transition: all 0.3s;
}
.footer__list a:hover {
  opacity: 0.7;
}
.footer__copy {
  margin: 50px 0 0;
  text-align: right;
  font-size: 26px;
  color: #f1cb90;
}

@media (max-width: 1500px) {
  .nav__logo {
    margin-right: -15px;
    max-width: 270px;
  }
  .catalog h1 {
    font-size: 60px;
    margin: 35px 0 0;
    letter-spacing: 0.33rem;
  }
  .catalog__subtitle {
    font-size: 42px;
    letter-spacing: 0.26rem;
  }
  .catalog__title {
    font-size: 26.8px;
    letter-spacing: 0.17rem;
  }
  .catalog__wrapper {
    gap: 60px;
    margin: 90px 0 0;
  }
  .catalog__item p {
    font-size: 25px;
  }
}
@media (max-width: 1024px) {
  .catalog__wrapper {
    gap: 20px;
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 768px) {
  .nav {
    padding: 30px 0 0;
  }
  .catalog h1 {
    font-size: 42px;
    letter-spacing: 0.23rem;
    margin: 25px 0 0;
  }
  .catalog__subtitle {
    font-size: 30px;
    letter-spacing: 0.16rem;
  }
  .catalog__title {
    font-size: 18px;
    letter-spacing: 0.14rem;
  }
  .catalog__wrapper {
    margin: 50px 0 0;
  }
  .catalog__item {
    padding: 10px;
  }
  .catalog__item p {
    font-size: 16px;
    padding: 0 0 10px;
  }
  .footer {
    padding: 50px 0;
  }
  .footer__list {
    flex-direction: column;
    align-items: end;
    gap: 30px;
  }
  .footer__list a {
    font-size: 23px;
    text-align: right;
    display: block;
  }
  .footer__copy {
    font-size: 23px;
    margin: 30px 0 0;
  }
}
@media (max-width: 600px) {
  .nav {
    padding: 16px 0 0;
  }
  .nav__logo {
    max-width: 133px;
    margin-right: -8px;
  }
  .catalog {
    padding: 3px 0 0;
  }
  .catalog h1 {
    font-size: 20px;
    letter-spacing: 0.108rem;
    margin: 10px 0 0;
  }
  .catalog__subtitle {
    font-size: 15px;
    letter-spacing: 0.05rem;
  }
  .catalog__title {
    font-size: 10px;
    letter-spacing: 0.019rem;
  }
  .catalog__item p {
    font-size: 12px;
  }
  .footer__list {
    gap: 10px;
  }
  .footer__list a {
    font-size: 16px;
  }
  .footer__copy {
    font-size: 16px;
    margin: 10px 0 0;
  }
}